.Footer {
    margin: 0 3.3%;
    padding-top: 20px;
    border-top: solid 1px #cbcbcb;
}

.FooterSignupContainer {

}

.FooterSignUpLabel {
    display: block;
    font-size: 16px;
    padding-bottom: 10px;
    text-transform: uppercase;
}

.FooterSignUp {
    display: block;
    font-size: 16px;
    padding: 5px 10px;
    box-sizing: border-box;
    border: solid 1px black;
    width: 100%;
}

.FooterSignUp::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #cbcbcb;
    opacity: 1; /* Firefox */
}

.FooterSignUp:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #cbcbcb;
}

.FooterSignUp::-ms-input-placeholder { /* Microsoft Edge */
    color: #cbcbcb;
}

.FooterTextContainer {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
}

@media only screen and (min-width: 768px) {
    .Footer {
        margin: 0 5.5%;
    }
}